





















@import '../../../styles/vars.scss';

#lite-footer {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  text-shadow: 0 0 .5rem mix($--black, $--primary-color, 50%);
  padding: 1rem 0 .5rem 0;

  > * {
    margin-top: 0;
    margin-bottom: .5rem;
    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: inherit;
    transition: transform 100ms;
    &:hover {
      transform: scale(1.1);
    }
  }
}

.icon-list {
  display: flex;
  > a {
    margin: 0 0.5rem;
  }
}

.powered-by {
  font-size: 0.8em;
}

.title {
  font-size: 1.3em;
}
