






































@import '../../../styles/vars.scss';

#top-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  z-index: 1000;
  overflow: hidden;
  color: white;
  text-shadow: 1px 1px 8px #444;
  .main-menu {
    width: 100%;
  }

}

.dropdown-item {
  display: flex;
  align-items: center;

  .icon {
    width: 1rem;
    text-align: center;
    margin-right: 0.3rem;
  }

  .item-name {
    flex-grow: 1;
  }
}

.blur {
  display: flex;
  align-items: center;
}

.menu-btn > span {
  margin-right: 0.2rem;

}

.item-icon {
  display: inline-block;
  width: 1rem;
  text-align: center;
}

.el-menu--horizontal {
  display: flex;
  justify-content: center;

  .el-menu-item {
    line-height: 40px;
    height: 40px;
    text-shadow: 0 0 2px white;
    color: mix($--white, $--primary-color, 35%);

    i {
      color: inherit;
    }

    &:hover,
    &:focus {
      background-color: rgba(255, 255, 255, 0);
    }

    * {
      vertical-align: unset;
    }
  }

  .is-active {
    color: $--primary-color;
  }
}

.el-menu {
  background-color: rgba(255, 255, 255, 0);
}

